import type { CMSList } from '@globalTypes/CMSList';

import { bindQuotationData } from './bindQuotationData';
import { buildQueryParams } from './buildQueryParams';
import { apiBaseUrl, SELECTORS, SNIPPET_CODE_TO_HOTEL } from './constants';
import { getLocale, listenForLanguageChange, offerSlug } from './utils';

export class Offers {
  private hotelsIds: string[][];
  private listInstance: CMSList;
  private itemTemplateElement: HTMLDivElement;
  private loadingSpinner: HTMLElement | null;
  private cmsLoadEmptyState: HTMLDivElement | null;
  private offerLookup: { [key: string]: CMSOfferItem } = {};

  constructor(listInstance: CMSList, itemTemplateElement: HTMLDivElement, hotelsIds: string[]) {
    this.cmsLoadEmptyState = document.querySelector<HTMLDivElement>(SELECTORS.cmsFilterEmptyState);
    if (this.cmsLoadEmptyState) {
      this.cmsLoadEmptyState.classList.add('hide-empty-state');
    }

    this.listInstance = listInstance;
    this.itemTemplateElement = itemTemplateElement;

    // split the hotelids into two for easy queries
    const midPoint = 2;
    const secondHalf = hotelsIds.splice(midPoint);
    this.hotelsIds = [hotelsIds, secondHalf];

    // Remove existing items
    listInstance.clearItems();

    listenForLanguageChange();
    this.loadingSpinner = document.querySelector<HTMLElement>(SELECTORS.loadingSpinner);
  }

  /**
   * Retrieve hotel offer for given hotel IDs
   * @param locale to query the data with
   */
  public async getGroupOffers(options?: {
    clearItems?: boolean;
    filter?: (value: CMSOfferItem) => boolean;
  }) {
    const { clearItems = true, filter } = options || {};

    if (clearItems) this.listInstance.clearItems();

    // get the cms offers data
    const cmsOffersData = await this.getCMSOffers();
    if (!cmsOffersData) return;

    // filter the cms offers data using the provided filter
    const filteredOffers = cmsOffersData.items.filter(filter || (() => true));

    // generate the offer metadata and create a lookup object for the cms offers data (useful when looking up for offers discount percentage)
    const {
      offerLookup,
      offerMetaData,
    }: { offerLookup: { [key: string]: CMSOfferItem }; offerMetaData: string[] } =
      this.generateOfferData(filteredOffers);

    // store the lookup object for future reference
    this.offerLookup = offerLookup;

    for (const hotelgroupIds of this.hotelsIds) {
      const hids = hotelgroupIds.join(',');

      // get the group offers data
      const hotelOffersData = await this.getGroupOffersData({
        hids: hids,
        locale: getLocale(),
      });

      // Create rate items for each hotel and add it to the collection
      this.addHotelToCollection(hotelOffersData, filter ? offerMetaData : undefined);
    }
    if (this.loadingSpinner) this.loadingSpinner.style.display = 'none';
    if (this.cmsLoadEmptyState) {
      this.cmsLoadEmptyState.classList.remove('hide-empty-state');
    }
  }

  /**
   * Retrieve hotel offer for given offer category
   * @param locale to query the data with
   */
  public async getGroupCategoryOffers(categoryId: string) {
    const filter = (value: CMSOfferItem) => {
      const { isArchived, isDraft, fieldData } = value;
      return !isArchived && !isDraft && fieldData.category === categoryId;
    };
    this.getGroupOffers({ filter });
  }

  /**
   * Retrieve hotel offer for given offer theme
   * @param locale to query the data with
   */
  public async getGroupThemeOffers(themeId: string) {
    const filter = (value: CMSOfferItem) => {
      const { isArchived, isDraft, fieldData } = value;
      return !isArchived && !isDraft && !!fieldData.thematiques?.includes(themeId);
    };
    this.getGroupOffers({ filter });
  }

  /**
   *
   * @param filteredOffers to generate offer data from
   * @returns
   */
  private generateOfferData(filteredOffers: CMSOfferItem[]) {
    const offerMetaData: string[] = [];
    const offerLookup: { [key: string]: CMSOfferItem } = {};

    filteredOffers.forEach((offer) => {
      const {
        fieldData: { 'hotel-or-property-api-id': hotelId, 'offer-api-name': name },
      } = offer;
      const slug = offerSlug(hotelId, name);
      offerMetaData.push(slug);

      offerLookup[slug] = offer;
    });
    return { offerLookup, offerMetaData };
  }

  /**
   *
   * @param hotelOffersData to add to the collection
   */
  private addHotelToCollection(hotelOffersData: GroupOffersData | null, offerMetaData?: string[]) {
    if (hotelOffersData) {
      for (const { prop, rates } of hotelOffersData.data) {
        let filteredRates = rates;
        if (offerMetaData) {
          filteredRates = rates.filter((rate) => {
            const { title, name } = rate.rate;
            const { hid } = prop;
            return (
              offerMetaData.includes(offerSlug(hid, name)) ||
              offerMetaData.includes(offerSlug(hid, title))
            );
          });
          // filteredRates.forEach((rate) => {
          //   console.log(`=====${rate.rate.name}  ==== ${rate.rate.title} ====`);
          // });
        }
        const items = filteredRates.map((item) =>
          this.createItem(prop, item, this.itemTemplateElement)
        );
        if (items.length > 0) {
          this.listInstance.addItems(items);
        }
      }
    }
  }

  /**
   *
   * @param queryParams to include as part of query for the request
   * @returns Starting price data
   */
  private async getGroupOffersData(queryParams: QueryParams): Promise<GroupOffersData | null> {
    const url = apiBaseUrl + 'groupOffers' + buildQueryParams(queryParams);

    const response = await fetch(url);
    const body = await response.json();
    if (body.error || !body.data) return null;

    return body as GroupOffersData;
  }

  /**
   *
   * @param queryParams to include as part of query for the request
   * @returns Starting price data
   */
  private async getCMSOffers(): Promise<CMSOffersData | null> {
    const url = apiBaseUrl + 'cmsOffers';

    const response = await fetch(url);
    const body = await response.json();
    if (!body.items) return null;

    return body as CMSOffersData;
  }

  /**
   * Creates an item from the template element.
   * @param rate The Rate data to create the item from.
   * @param templateElement The template element.
   *
   * @returns A new Collection Item element.
   */
  private createItem(offerProperty: OfferProperty, rate: Rate, templateElement: HTMLDivElement) {
    const { rate: rateData, quotation } = rate;
    const { hid, title: hotelName } = offerProperty;
    const { image, title, plain_description } = rateData;
    const offerCMSData = this.offerLookup[offerSlug(hid, title)];

    const { destination, slug } = SNIPPET_CODE_TO_HOTEL[hid];

    // Clone the template element
    const newItem = templateElement.cloneNode(true) as HTMLDivElement;

    this.bindOffersMetaData(newItem, { destination, image, hotelName, title, slug });
    bindQuotationData(newItem, quotation, hid, offerCMSData);

    const moreDetails = newItem.querySelector<HTMLDivElement>(SELECTORS.moreDetails);
    if (moreDetails) {
      moreDetails.addEventListener('click', () => {
        const offersPopupEl = newItem.querySelector<HTMLDivElement>(SELECTORS.offersPopup);
        if (offersPopupEl) {
          this.bindOffersMetaData(offersPopupEl, {
            destination,
            image,
            hotelName,
            title,
            slug,
            description: plain_description,
          });
          bindQuotationData(offersPopupEl, quotation, hid, offerCMSData);

          offersPopupEl.style['opacity'] = '1';
          offersPopupEl.style['display'] = 'flex';

          const popupCloseBtn = newItem.querySelector<HTMLElement>(SELECTORS.offersPopupClose);
          if (popupCloseBtn) {
            popupCloseBtn.addEventListener('click', () => {
              offersPopupEl.style['opacity'] = '0';
              offersPopupEl.style['display'] = 'none';
            });
          }
        }
      });
    }

    newItem.style.display = 'flex';
    return newItem;
  }

  /**
   *
   * @param container for data item
   * @param destination for offer
   * @param image for offer
   * @param hotelName for hotel
   * @param title for offer
   */
  private bindOffersMetaData(
    container: HTMLDivElement,
    {
      destination,
      image,
      hotelName,
      title,
      slug,
      description,
    }: {
      destination: string;
      image: Image;
      hotelName: string;
      title: string;
      slug: string;
      description?: string;
    }
  ) {
    const destinationEl = container.querySelector<HTMLElement>(SELECTORS.destination);
    const imageEl = container.querySelector<HTMLImageElement>(SELECTORS.image);
    const hotelNameEl = container.querySelector<HTMLElement>(SELECTORS.hotelName);
    const nameEl = container.querySelector<HTMLElement>(SELECTORS.name);
    const descriptionEl = container.querySelector<HTMLElement>(SELECTORS.description);
    const hotelLinkEl = container.querySelector<HTMLAnchorElement>(SELECTORS.hotelLink);

    if (destinationEl) destinationEl.textContent = destination;
    if (imageEl) imageEl.src = image?.url;
    if (hotelNameEl) hotelNameEl.innerHTML = hotelName;
    if (nameEl) nameEl.innerHTML = title;
    if (descriptionEl && description) descriptionEl.textContent = description;
    if (hotelLinkEl && slug) {
      hotelLinkEl.addEventListener('click', () => {
        window.location.href = slug;
      });
    }
  }
}
