import { CURRENCY_TO_SYMBOL, SELECTORS } from './constants';
import { hotelAndFlightURL } from './utils';

/**
 *
 * @param quotation data to bind
 * @param container for data items
 * @param hotelId for hotel
 * @param cmsOfferData for offer filtering
 */
export const bindQuotationData = (
  container: HTMLDivElement,
  quotation: Quotation,
  hotelId: string,
  cmsOfferData?: CMSOfferItem
) => {
  const { pricePerNight, currency, plainBookLink } = quotation;

  const priceEl = container.querySelector<HTMLElement>(SELECTORS.price);
  if (priceEl) priceEl.innerHTML = pricePerNight;

  const currencyEl = container.querySelector<HTMLElement>(SELECTORS.currency);
  if (currencyEl) currencyEl.innerHTML = CURRENCY_TO_SYMBOL[currency];

  const hotelReserveEls = container.querySelector<HTMLLinkElement>(SELECTORS.hotelReserveLink);
  if (hotelReserveEls) {
    hotelReserveEls.addEventListener('click', (e) => {
      e.preventDefault();
      e.stopPropagation();
      window.open(plainBookLink, '_blank');
    });
  }

  const hotelVillaReserveEl = container.querySelector<HTMLLinkElement>(
    SELECTORS.hotelVillaReserveLink
  );
  if (hotelVillaReserveEl) {
    hotelVillaReserveEl.addEventListener('click', (e) => {
      e.preventDefault();
      e.stopPropagation();
      window.open(hotelAndFlightURL(currency, hotelId), '_blank');
    });
  }

  if (cmsOfferData) {
    const {
      fieldData: { 'discount-percentage': discountPercentage },
    } = cmsOfferData;
    const discountEl = container.querySelector<HTMLElement>(SELECTORS.discount);
    if (discountEl && discountPercentage) discountEl.innerHTML = `-${discountPercentage}%`;

    const discountContainerEl = container.querySelector<HTMLDivElement>(
      SELECTORS.discountContainer
    );

    if (discountContainerEl && !discountPercentage) {
      discountContainerEl.style.display = 'none';
    }
  }
};
