import type { CMSFilters } from '@globalTypes/CMSFilters';
import type { CMSList } from '@globalTypes/CMSList';

import { BE_HID_TO_SNIPPET_CODE } from './constants';
import { Offers } from './Offers';

export const createOffersInstance = (filtersInstances: CMSFilters[]) => {
  const [filtersInstance] = filtersInstances;

  // Get the list instance
  const { listInstance } = filtersInstance;

  return createOfferForList(listInstance);
};

export const createOfferForList = (listInstance: CMSList) => {
  const [firstItem] = listInstance.items;

  // Save a copy of the template
  const itemTemplateElement = firstItem.element;

  // We don't have permission to view the offers here yet
  const hotelsToExlude = ['gpgua27143', 'mqsai18593'];
  const hotelIDs = Object.values(BE_HID_TO_SNIPPET_CODE).filter(
    (id) => !hotelsToExlude.includes(id)
  );

  const offers = new Offers(listInstance, itemTemplateElement, hotelIDs);
  return offers;
};
