import {
  hotelAndFlightURLBaseUrl,
  LANG_TO_LOCALE,
  SELECTORS,
  SNIPPET_CODE_TO_HOTEL,
} from './constants';

export const listenForLanguageChange = () => {
  const languageDropdown = document.querySelector<HTMLSelectElement>(SELECTORS.languageDropdown);

  const languageLinks = languageDropdown?.querySelectorAll<HTMLAnchorElement>('a');
  languageLinks?.forEach((link) => {
    link.addEventListener('click', () => {
      //window.location.reload();
    });
  });
};

/**
 *
 * @returns The locale of the current language.
 */
export const getLocale = () => {
  const lang = getLanguage();
  return LANG_TO_LOCALE[lang];
};

/**
 *
 * @returns the current language.
 */
export const getLanguage = () => {
  return localStorage.getItem('wglang') || 'fr';
};

/**
 *
 * @returns A map of slug to hotel id.
 */
export const slugToHotelIdMap = () => {
  const map: { [key: string]: string } = {};
  Object.entries(SNIPPET_CODE_TO_HOTEL).forEach(([key, value]) => {
    map[value.slug] = key;
  });
  return map;
};

export const offerSlug = (hotelId: string, offerName: string) => {
  return `${hotelId.toLowerCase().trim()}-${offerName.toLowerCase().trim()}`;
};

export const hotelAndFlightURL = (currency: string, hotelID: string) => {
  const lang = getLanguage().toUpperCase();
  const hotelCode = SNIPPET_CODE_TO_HOTEL[hotelID].codeIPlanet.toUpperCase();
  return `${hotelAndFlightURLBaseUrl}?Lang=${lang}&currency=${currency.toUpperCase()}&HotelCode=${hotelCode}`;
};
